<template>
    <div class="playlists">
        <div class="playlist-top">
            <div class="left">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-bofang1"></use>
                </svg>
                <div class="text">
                    <div class="title">播放全部</div>
                    <div class="num">（共{{playlist.tracks.length}}首）</div>
                </div>
            </div>
            <div class="btn">
                + 收藏（{{changeValue(playlist.subscribedCount)}}）
            </div>
        </div>
        <div class="list">
            <div class="playItem" v-for="(item,i) in playlist.tracks" :key="i">
                <div class="left" @click="play(i)">
                    <div class="index">{{i+1}}</div>
                    <div class="content">
                        <div class="title">{{item.name}}</div>
                        <div class="author">
                            <div class="tag" v-for="(tag,index) in playlist.tags" :key="index">{{tag}}</div>
                            <div class="singer">
                                <div class="discre" v-for="(dd) in playlist.tracks[i].ar">{{(dd.name)+''}}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="right">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-tuya-"></use>
                    </svg>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-gengduo"></use>
                    </svg>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {useStore} from 'vuex'
    export default {
        name: '',
        props:['playlist'],
        setup(){
            const store = useStore()
            const play = (value)=>{
                //触发mutations
                store.commit('setPlayIndex',value)
                console.log(value)
            }
            function changeValue(num){
                if (num>=100000000){
                    return (num/100000000).toFixed(2)+'亿'
                } else if(num>=10000){
                    return (num/10000).toFixed(2)+'万'
                }else {
                    return num
                }
            }
            return{
                changeValue,play
            }
        }
    }
</script>

<style lang="less" scoped>
.playlists{
    width: 7.5rem;
    padding: 0 0.4rem;
    background-color: white;
    border-top-left-radius: 0.3rem;
    border-top-right-radius: 0.3rem;
    margin-top: 0.4rem;
    padding-bottom: 1rem;
    .playlist-top{
        display: flex;
        justify-content: space-between;
        height: 1rem;
        align-items: center;
        .left{
            display: flex;
            align-items: center;
            .index{

            }
            .icon{
                width: 0.4rem;
                height: 0.4rem;
            }
            .title{
                font-size: 0.3rem;
                font-weight: 900;
            }
            .num{
                font-size: 0.25rem;
                color: #666666;
            }
            .text{
                display: flex;
                align-items: center;
                margin-left: 0.2rem;
            }
        }
        .btn{
            font-size: 0.25rem;
            color: white;
            padding: 0.1rem;
            line-height: 0.6rem;
            background-color: orangered;
            border-radius: 0.4rem;
        }
    }
    .list{
        .playItem{
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 1rem;
            .left{
                display: flex;
                align-items: center;
                .index{
                    color: #666666;
                    font-size: 0.3rem;
                    width: 0.2rem;
                }
                .content{
                    margin-left: 0.4rem;
                }
                .title{
                    width: 4rem;
                    font-size: 0.3rem;
                    font-weight: 900;
                    color: #000000;
                    margin-bottom: 0.1rem;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                }
                .author{
                    color: #666666;
                    display: flex;
                    height: 0.3rem;
                    line-height: 0.28rem;

                    .tag{
                        font-size: 0.1rem;
                        color: orangered;
                        border: 0.7px solid orangered;
                        border-radius: 0.05rem;
                        margin-right: 0.07rem;

                    }
                    .singer{
                        display: flex;
                        width: auto;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                        .discre{
                            width: 2rem;
                            display: flex;
                            font-size: 0.25rem;
                            text-overflow:ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 1;
                            -webkit-box-orient: vertical;
                        }
                    }

                }

            }
            .right{
                .icon{
                    margin: 0 0.2rem;
                    width: 0.45rem;
                    height: 0.45rem;
                    line-height: 1rem;
                }
            }
        }
    }
}

</style>