<template>
    <div class="playController">
        <div class="left" @click="show=!show">
            <img :src="playlist[playCurrentIndex].al.picUrl">
            <div class="content">
                <div class="title">{{playlist[playCurrentIndex].name}}</div>
                <div class="tips">横划可以切换上下首</div>
            </div>
        </div>
        <div class="right">
            <svg v-show="paused" class="icon" aria-hidden="true" @click="play">
                <use xlink:href="#icon-bofang_o"></use>
            </svg>
            <svg v-show="!paused" class="icon" aria-hidden="true" @click="play">
                <use xlink:href="#icon-zanting_o"></use>
            </svg>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-bofangliebiao"></use>
            </svg>
        </div>
        <play-music @playChange="showChangePlay" ref="playmusic" :paused="paused" :play="play" @back="show=!show" v-show="show" :playDetail="playlist[playCurrentIndex]"></play-music>
        <audio ref="audio" @ended="overAudio" :src="`https://music.163.com/song/media/outer/url?id=${playlist[playCurrentIndex].id}.mp3`"></audio>
    </div>
</template>

<script>
    import {ref,onMounted,onUpdated, } from "vue"
    import {mapState,mapMutations} from 'vuex'
    import playMusic from "./playMusic.vue";
    export default {
        computed:{
            ...mapState(['playlist','playCurrentIndex'])
        },
        data(){
            return {
                paused: true,
                show: false
            }
        },
        methods:{
            play:function () {
                if (this.$refs.audio.paused){
                    this.$refs.audio.play()
                    this.UpdateTime()

                }else{
                    this.$refs.audio.pause()
                    clearInterval(this.$store.state.id)
                }
                this.paused=!this.paused
            },
            UpdateTime(){
                this.$store.state.id=setInterval(()=>{
                    this.$store.commit('setCurrentTime',this.$refs.audio.currentTime)
                },1000)
            },
            overAudio(){
                this.paused=!this.paused
                this.UpdateTime()

            },
            showChangePlay(bol){
                this.paused=bol
                // this.UpdateTime()

            }
        },
        components:{
            playMusic
        },
        mounted(){
          this.$store.dispatch('reqLyric',{id:this.playlist[this.playCurrentIndex].id})
        },
        updated(){
            this.$store.dispatch('reqLyric',{id:this.playlist[this.playCurrentIndex].id})
        }
        // setup(){
        //     let audio=ref(null);
        //     // 需要在生命周期获取
        //     onMounted(()=>{
        //         // 当界面挂载出来后就会自动执行
        //         console.log(audio.value);
        //     })
        //     onUpdated(()=>{
        //         console.log(audio.value);
        //     })
        //     function play(){
        //         if (this.audio.paused){
        //             this.audio.play()
        //         }else{
        //             this.audio.pause()
        //         }
        //
        //     }
        //     return{
        //         play,audio
        //     }
        // }
    }
</script>

<style lang="less" scoped>
.playController{
    width: 7.5rem;
    height: 1rem;
    padding-right: 0.4rem;
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    z-index: 10;
    align-items: center;
    border-top: 1px solid #cccccc;
    background-color: white;
    .left{
        display: flex;
        padding: 0 0.2rem;
        img{
            width: 0.8rem;
            height: 0.8rem;
            border-radius: 0.4rem;
        }
        .content{
            padding: 0 0.2rem;
            .title{
                width: 3.7rem;
                overflow: hidden;
                text-overflow:ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
            }
            .tips{
                font-size: 0.2rem;
                color: #999999;
            }
        }
    }
    .right{
        .icon{
            width: 0.5rem;
            height: 0.5rem;
            margin: 0 0.2rem;
        }
    }
}
</style>