import axios from "axios";
//获取轮播图的api，type资源类型,对应以下类型,默认为 0 即 PC
// 0: pc
// 1: android
// 2: iphone
// 3: ipad
// let baseUrl='http://localhost:3000'
let baseUrl='https://music-api-lovat.vercel.app'
export function getBanner(type=0){
    return axios.get(`${baseUrl}/banner?type=${type}`)
}

//获取推荐歌单
//可选参数 : limit: 取出数量 , 默认为 30 (不支持 offset)
export function getMusicList(limit=10) {
    return axios.get(`${baseUrl}/personalized?limit=${limit}`)
    
}
//获取歌单详情

export function getPlayListDetail(id) {
    return axios.get(`${baseUrl}/playlist/detail?id=${id}`)

}
//获取歌单
export function getLyric(id) {
    return axios.get(`${baseUrl}/lyric?id=${id}`)
}
//搜索歌曲
export function searchMusic(keyWord) {
    return axios.get(`${baseUrl}/cloudsearch?keywords=${keyWord}`)
}
//手机登录
export function phoneLogin(phone,password) {
    return axios.get(`${baseUrl}/login/cellphone?phone=${phone}&password=${password}`)
}
//获取用户详情
export function userDetail(uid) {
    return axios.get(`${baseUrl}/user/detail?uid=${uid}`)
}

export default {
    getBanner,getMusicList,getPlayListDetail,getLyric,searchMusic,phoneLogin,userDetail
}