<template>
    <div class="searchTop" >
        <div class="searchTopNav">
            <div class="back">
                <svg class="icon" aria-hidden="true" @click="$router.go(-1)">
                    <use xlink:href="#icon-zuojiantou"></use>
                </svg>
            </div>
            <div class="right">
                <input type="text" v-model="searchKeyword" :placeholder="placeholder" @keydown.enter="saveKeyWord">
            </div>
        </div>
        <div class="history" v-show="searchSongs.length===0">
            <div class="historyLeft">
                历史
            </div>
            <div class="historyRight">
                <div @click="historySearch(item)" class="keywordItem" v-for="(item,i) in keywordList" :key="i">{{item}}</div>
            </div>
        </div>
        <div class="searchSongs" v-show="searchSongs.length!==0">
            <div class="searchSongs-top">
                <div class="left">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-bofang1"></use>
                    </svg>
                    <div class="text">
                        <div class="title">播放全部</div>
                        <div class="num">（共{{searchSongs.length}}首）</div>
                    </div>
                </div>
            </div>
            <div class="list">
                <div class="playItem" v-for="(item,i) in searchSongs" :key="i">
                    <div class="left" @click="setPlay(item,i)">
                        <div class="index">{{i+1}}</div>
                        <div class="content">
                            <div class="title">{{item.name}}</div>
                            <div class="author">
                                <!--                                <div class="tag" v-for="(tag,index) in searchSongs.tags" :key="index">{{tag}}</div>-->
                                <div class="singer">
                                    <div class="discre" v-for="(dd,i) in item.ar">{{dd.name}}</div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="right">
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-tuya-"></use>
                        </svg>
                        <svg class="icon" aria-hidden="true">
                            <use xlink:href="#icon-gengduo"></use>
                        </svg>
                    </div>
                </div>
            </div>
        </div>

    </div>

</template>

<script>
    import {searchMusic} from '../api/index.js'
    import {mapMutations} from 'vuex'
    export default {
        name: '',
        data(){
            return{
                placeholder:"eson",
                keywordList:[],
                searchKeyword:'',
                searchSongs:[]
            }
        },
        beforeMount(){
            this.keywordList=localStorage.keywordList ? JSON.parse(localStorage.keywordList) : []
        },
        methods:{
            async saveKeyWord(){
                if (this.searchKeyword!=''){
                    this.keywordList.push(this.searchKeyword)
                    this.keywordList=Array.from(new Set(this.keywordList))
                    if (this.keywordList.length>10){
                        this.keywordList=this.keywordList.splice(this.keywordList.length-10,this.keywordList)
                    }
                    localStorage.keywordList=JSON.stringify(this.keywordList)
                    let result=await searchMusic(this.searchKeyword)
                    this.searchSongs=  result.data.result.songs
                    console.log(this.searchSongs)
                }else {
                    this.searchSongs=''
                }
            },
            historySearch(keyWord){
                this.searchKeyword=keyWord
                this.saveKeyWord()
            },
            setPlay(item,i){
                this.$store.commit('pushPlaylist',item)
                this.$store.commit('setPlayIndex',this.$store.state.playlist.length-1)
            }
        }
    }
</script>

<style lang="less" scoped>
.searchTop{
    width: 7.5rem;
    height: calc(100vh - 1.2rem);
    padding:0 0.4rem;
    .searchTopNav{
        display: flex;
        width: 100%;
        height: 1.2rem;
        align-items: center;
        position: fixed;
        padding:0 0.4rem;
        top: 0;
        left: 0;
        background-color: #ffffff;
        .icon{
            width: 0.6rem;
            height: 0.6rem;
        }
        .back{
            margin-right: 0.2rem;
        }
        .right{
            flex: 1;
            input{
                border: none;
                outline: none;
                border-bottom: 1px solid #666666;
                width: 100%;
                height: 0.5rem;
            }
        }
    }
    .history{
        margin-top: 1.2rem;
        display: flex;
        .historyLeft{
            width: 0.8rem;
            height: 0.6rem;
            font-weight: 900;
            margin:0.2rem 0rem;
            line-height: 0.6rem;
        }
        .historyRight{
            color: #666666;
            display: flex;
            /*自动换行*/
            flex-wrap: wrap;
            flex: 1;
            .keywordItem{
                background-color: #eee;
                height: 0.6rem;
                padding: 0 0.2rem;
                border-radius: 0.4rem;
                line-height: 0.6rem;
                margin:0.1rem 0.1rem 0 0.1rem;
            }
        }
    }
    .searchSongs{
        width: 6.7rem;
        padding: 1.2rem 0 1rem 0;
        background-color: white;
        .searchSongs-top{
            display: flex;
            justify-content: space-between;
            height: 1rem;
            align-items: center;
            .left{
                display: flex;
                align-items: center;
                .index{

                }
                .icon{
                    width: 0.4rem;
                    height: 0.4rem;
                }
                .title{
                    font-size: 0.3rem;
                    font-weight: 900;
                }
                .num{
                    font-size: 0.25rem;
                    color: #666666;
                }
                .text{
                    display: flex;
                    align-items: center;
                    margin-left: 0.2rem;
                }
            }
        }
        .list{
            .playItem{
                display: flex;
                align-items: center;
                justify-content: space-between;
                height: 1rem;
                .left{
                    display: flex;
                    align-items: center;
                    .index{
                        color: #666666;
                        font-size: 0.3rem;
                        width: 0.2rem;
                    }
                    .content{
                        margin-left: 0.4rem;
                    }
                    .title{
                        width: 4rem;
                        font-size: 0.3rem;
                        font-weight: 900;
                        color: #000000;
                        margin-bottom: 0.1rem;
                        overflow: hidden;
                        text-overflow:ellipsis;
                        display: -webkit-box;
                        -webkit-line-clamp: 1;
                        -webkit-box-orient: vertical;
                    }
                    .author{
                        color: #666666;
                        display: flex;
                        height: 0.3rem;
                        line-height: 0.28rem;

                        .tag{
                            font-size: 0.1rem;
                            color: orangered;
                            border: 0.7px solid orangered;
                            border-radius: 0.05rem;
                            margin-right: 0.07rem;

                        }
                        .singer{
                            display: flex;
                            width: auto;
                            overflow: hidden;
                            text-overflow:ellipsis;
                            display: -webkit-box;
                            -webkit-line-clamp: 1;
                            -webkit-box-orient: vertical;
                            .discre{
                                width: 2rem;
                                display: flex;
                                font-size: 0.25rem;
                                text-overflow:ellipsis;
                                display: -webkit-box;
                                -webkit-line-clamp: 1;
                                -webkit-box-orient: vertical;
                            }
                        }

                    }

                }
                .right{
                    .icon{
                        margin: 0 0.2rem;
                        width: 0.45rem;
                        height: 0.45rem;
                        line-height: 1rem;
                    }
                }
            }
        }
    }
}


</style>