<template>
    <div class="listViewTop">
        <img class="bg" :src="playlist.coverImgUrl">
        <div class="listViewTopNav">
            <div class="back">
                <svg class="icon" aria-hidden="true" @click="$router.go(-1)">
                    <use xlink:href="#icon-zuojiantou"></use>
                </svg>
                <div class="title">
                    歌单
                </div>
            </div>
            <input style="display: none" @click="showInput">
            <div class="right">
                <svg class="icon search" aria-hidden="true">
                    <use xlink:href="#icon-sousuo"></use>
                </svg>
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-gengduo"></use>
                </svg>
            </div>
        </div>
        <div class="content">
            <div class="contentLeft">
                <img :src="playlist.coverImgUrl">
                <div class="count">
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-play"></use>
                    </svg>
                    <span>{{changeValue(playlist.playCount)}}</span>
                </div>
            </div>
            <div class="contentRight">
                <h3>{{playlist.name}}</h3>
<!--                v-if的作用：这里的问题是因为一开始渲染的时候数据还没来，等数据来了之后又重新渲染了一遍，所以我们既看到了数据有看到了报错。-->
<!--                <div class="author" v-if="playlist.creator">-->
                <div class="author">
                    <img class="head" :src="playlist.creator.avatarUrl">
                    <span>{{playlist.creator.nickname}}</span>
                </div>
                <div class="context">
                    <div class="description">
                        {{playlist.description}}
                    </div>
                    <svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-youjiantou"></use>
                    </svg>
                </div>
            </div>
        </div>
        <div class="iconList">
            <div class="iconItem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-pinglun1"></use>
                </svg>
                <span>{{playlist.commentCount}}</span>
            </div>
            <div class="iconItem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-fenxiang"></use>
                </svg>
                <span>{{playlist.shareCount}}</span>
            </div>
            <div class="iconItem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-xiazai"></use>
                </svg>
                <span>下载</span>
            </div>
            <div class="iconItem">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-xuanzhong"></use>
                </svg>
                <span>多选</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: '',
        props:['playlist'],
        setup(){
            function changeValue(num) {
                if (num>=100000000){
                    return (num/100000000).toFixed(2)+'亿'
                } else if(num>=10000){
                    return (num/10000).toFixed(2)+'万'
                }
            }
        },
        methods:{
            changeValue(num){
                if (num>=100000000){
                    return (num/100000000).toFixed(2)+'亿'
                } else if(num>=10000){
                    return (num/10000).toFixed(2)+'万'
                }else {
                    return num
                }
            }
        }
    }
</script>

<style lang="less" scoped>
.listViewTop{
    width: 7.5rem;
    padding: 0 0.2rem;
    .bg{
        position: fixed;
        left: 0;
        top: 0;
        width: 7.5rem;
        height: auto;
        z-index: -1;
        filter: blur(40px);
    }
    .listViewTopNav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 1.2rem;
        font-size: 0.35rem;
        .back,.right{
            color: #ffffff;
            display: flex;
            .title{
                margin-left: 0.3rem;
            }
            .icon{
                width: 0.5rem;
                height: 0.5rem;
                fill: #ffffff;
            }
            .search{
                margin-right: 0.3rem;
            }
        }
    }
    .content{
        display: flex;
        justify-content: space-between;
        padding: 0.4rem 0;
        .contentLeft{
            position: relative;
            img{
                width: 2.8rem;
                height: 2.8rem;
                border-radius: 0.1rem;
            }
            .count{
                position: absolute;
                right: 0.1rem;
                top: 0.05rem;
                font-size: 0.15rem;
                color: #ffffff;
                display: flex;
                align-items: center;
                font-weight: 900;
                .icon{
                    /*svg颜色修改*/
                    fill: #cccccc;
                }
            }
        }
        .contentRight{
            width: 3.8rem;
            h3{
                color: #ffffff;
            }
            .author{
                display: flex;
                align-items: center;
                margin: 0.2rem 0;
                img.head{
                    width: 0.6rem;
                    height: 0.6rem;
                    border-radius: 0.3rem;
                    margin-right: 0.2rem;
                }
                span{
                    color: #cccccc;
                    font-size: 0.26rem;
                }
            }
            .context{
                display: flex;

                .description{
                    width: 3.5rem;
                    font-size: 0.20rem;
                    color: #cccccc;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }
                .icon{
                    fill: #cccccc;
                    margin: 0.17rem 0 0 0.10rem;
                }
            }
        }
    }
    .iconList{
        display: flex;
        justify-content: space-around;
        .iconItem{
            display: flex;
            flex-direction: column;
            align-items: center;
            .icon{
                width: 0.6rem;
                height: 0.6rem;
                color: #ffffff;
            }
            span{
                font-size: 0.25rem;
                margin-top: 0.05rem;
                color: #cccccc;

            }
        }
    }
}

</style>