<template>
    <div class="musicList">
        <div class="musicList-top">
            <div class="title">发现好歌单</div>
            <div class="more">查看更多</div>
        </div>
        <div class="mlist">
            <div class="swiper-container" id="musicSwiper">
                <div class="swiper-wrapper">
                    <router-link :to="{path:'/listview',query:{id:item.id}}" class="swiper-slide" v-for="(item,i) in state.musicList" :key="i">
                        <img :src="item.picUrl" >
                        <div class="name">{{item.name}}</div>
                        <div class="count">
                            <svg class="icon" aria-hidden="true">
                                <use xlink:href="#icon-play"></use>
                            </svg>
                            {{changeValue(item.playCount)}}
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import 'swiper/css/swiper.css'
    import Swiper from 'swiper'
    import {getMusicList} from "../api/index.js";
    import {reactive,onMounted,onUpdated} from 'vue'
    //vue3
    export default {
        setup(){
            //reactive响应
            let state=reactive({musicList:[]})
            function changeValue(num) {
                if (num>=100000000){
                    return (num/100000000).toFixed(2)+'亿'
                } else if(num>=10000){
                    return (num/10000).toFixed(2)+'万'
                }
            }
            //挂载
            onMounted( async ()=>{
                let res=await getMusicList()
                state.musicList=res.data.result

                console.log(res)

            })
            onUpdated(()=>{
                var mySwiper = new Swiper('#musicSwiper', {
                    slidesPerView:3,
                    spaceBetween:10,
                });
            })
            return{
                state,changeValue
            }
        }
    }
    //vue2
    // export default {
    //     name: '',
    //     data:function(){
    //       return{
    //           musicList:[]
    //       }
    //     },
    //     methods:{
    //       changeValue:function (num) {
    //         if (num>=100000000){
    //             return (num/100000000).toFixed(2)+'亿'
    //         } else if(num>=10000){
    //             return (num/10000).toFixed(2)+'万'
    //         }
    //       }
    //     },
    //     mounted:async function () {
    //         let res=await getMusicList()
    //         console.log(res)
    //         this.musicList=res.data.result
    //
    //             // 如果需要分页器
    //             // pagination: {
    //             //     el: '.swiper-pagination',
    //             //     clickable:true,
    //             // },
    //     },
    //     updated(){
    //         var mySwiper = new Swiper('#musicSwiper', {
    //             slidesPerView:3,
    //             spaceBetween:10,
    //         });
    //     },
    // }
</script>

<style lang="less" scoped>
.musicList{
    width: 7.5rem;
    padding: 0 0.4rem;
    .musicList-top{
        display: flex;
        justify-content: space-between;
        height: 1rem;
        align-items: center;
        .title{
            font-size: 0.4rem;
            font-weight: 900;
        }
        .more{
            border: 1px solid #cccccc;
            border-radius: 0.2rem;
            font-size: 0.22rem;
            height: 0.4rem;
            width: 1.1rem;
            text-align: center;
            line-height: 0.4rem;
        }
    }
    .mlist{
        .swiper-container{
            width: 100%;
            height: 2.7rem;
            .swiper-slide{
                /*background-color: orange;*/
                display: flex;
                flex-direction: column;
                position: relative;
                img{
                    width: 100%;
                    height: auto;
                    border-radius: 0.1rem;
                }
                .name{
                    height: 0.6rem;
                    width: 100%;
                    font-size: 0.2rem;
                    line-height: 0.3rem;
                }
                .count{
                    position: absolute;
                    right: 0.1rem;
                    top: 0.05rem;
                    font-size: 0.15rem;
                    color: white;
                    display: flex;
                    align-content: center;
                    .icon{
                        /*svg颜色修改*/
                        fill: white;
                    }
                }
            }
        }
    }

}
</style>