<template>
    <div class="listView">
        <listview-top :playlist="state.playlist"></listview-top>
        <play-list :playlist="state.playlist"></play-list>
<!--        <play-controller></play-controller>-->

    </div>
</template>

<script>
    import {getPlayListDetail} from '../api/index.js'
    import {reactive,onMounted} from 'vue'
    import {useRoute} from 'vue-router'
    import listviewTop from "../components/listviewTop.vue";
    import playList from "../components/playList.vue";
    import store from "../store/index.js";
    // import playController from "../components/playController.vue";


    export default {
        name: '',
        setup(){
            const route=useRoute()
            let state= reactive({playlist:{
                    creator:{},
                    tracks:{
                        ar:{}
                    },
                }})
            onMounted(async ()=>{
                let id=route.query.id
                let res=await getPlayListDetail(id)
                state.playlist=res.data.playlist
                //传到store中的setPlaylist
                store.commit('setPlaylist',state.playlist.tracks)
                console.log(res)
                console.log(state.playlist.tracks)
            })
            return{
                state
            }
        },
        components:{
            listviewTop,
            playList,
            // playController
        }

    }
</script>

