<template>
    <div class="playMusic">
        <div class="bg" :style="{backgroundImage:`url(${playDetail.al.picUrl})`}"></div>
        <div class="playTop">
            <div class="back" @click="$emit('back')">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-zuojiantou"></use>
                </svg>
            </div>
            <div class="center">
                <div class="title">
<!--                    <marquee behavior="scroll" direction="left">{{playDetail.name}}</marquee>-->
                    <h3>{{playDetail.name}}</h3>
                </div>
            </div>
            <div class="share">
                <svg class="icon" aria-hidden="true">
                    <use xlink:href="#icon-fenxiang1"></use>
                </svg>
            </div>
        </div>
        <div class="playContent" v-show="!isLyric">
            <img class="needle" :class="{active:!paused}" src="../assets/img/needle-ab.png" alt="">
            <img class="disc" src="../assets/img/around.png" alt="">
            <img class="playImg" :src="playDetail.al.picUrl" alt="">
        </div>
        <div class="playLyric" v-show="isLyric" ref="playLyric">
            <p :class="{active:(currentTime*1000>=item.time&&currentTime*1000<item.pre)}" v-for="(item,i) in $store.getters.lyricList" :key="i">{{item.lyric}}</p>

        </div>
        <div class="progress"></div>
        <div class="playFooter">
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-xunhuan"></use>
            </svg>
            <svg class="icon" aria-hidden="true" @click="goPlay(-1),changePlay()">
                <use xlink:href="#icon-shangyishoushangyige"></use>
            </svg>
            <svg v-if="paused" class="icon play" aria-hidden="true" @click="play">
                <use xlink:href="#icon-bofang2"></use>
            </svg>
            <svg v-else class="icon play" aria-hidden="true" @click="play">
                <use xlink:href="#icon-zanting"></use>
            </svg>
            <svg class="icon" aria-hidden="true" @click="goPlay(1),changePlay,changePlay()">
                <use xlink:href="#icon-xiayigexiayishou"></use>
            </svg>
            <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-bofangliebiao2"></use>
            </svg>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: '',
        props:['playDetail','paused','play'],
        data:function () {
            return{
                isLyric:true
            }
        },
        computed:{
            ...mapState(['lyric','currentTime','playlist','playCurrentIndex']),

        },
        watch:{
            currentTime:function () {
                // console.log(newValue)
                // console.log([this.$refs.playLyric])
                let p=document.querySelector('p.active')
                if(p!=null){
                    this.$refs.playLyric.scrollTop=(p).offsetTop-180
                }
                console.log([p])
            }
        },
        methods:{
            goPlay(num) {
                let index=this.playCurrentIndex+num
                if(index<0){
                    index=this.playlist.length-1;
                }else if(index==this.playlist.length){
                    index=0
                }
                this.$store.commit('setPlayIndex',index)
            },
            changePlay(){
                this.$emit("playChange",true);
            }
        }

    }
</script>

<style lang="less" scoped>
.playMusic{
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: white;
    .bg{

        position: absolute;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        filter: blur(60px);
        background-size: auto 100%;
        background-position: center;

    }
    .playTop{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 7.5rem;
        height: 1.2rem;
        position: absolute;
        left: 0;
        top: 0;
        color: #ffffff;
        padding: 0 0.4rem;
        .icon{
            fill: #ffffff;
            width: 0.5rem;
            height: 0.5rem;
        }
        marquee{
            width: 5rem;
            flex: 1;
        }
    }
    .playContent{
        position: absolute;
        width: 7.5rem;
        height: 7.5rem;
        left: 0;
        top: 1.5rem;
        .needle.active{
            width: 1.8rem;
            height: auto;
            position: absolute;
            left: 3.5rem;
            transform-origin: 0.3rem 0;
            transform: rotate(-2deg);
            transition: all 1s;
            z-index: 9;
        }
        .needle{
            width: 1.8rem;
            height: auto;
            position: absolute;
            left: 3.5rem;
            transform-origin: 0.3rem 0;
            transform: rotate(-20deg);
            transition: all 1s;
            z-index: 9;
        }
        .disc{
            width: 5.5rem;
            height: 5.5rem;
            position: absolute;
            left: calc(50% - 2.7rem);
            top: 1.7rem;
        }
        .playImg{
            width: 3.5rem;
            height: 3.5rem;
            border-radius: 3.5rem;
            position: absolute;
            left: calc(50% - 1.7rem);
            top: 2.73rem;
        }
    }
    .playFooter{
        width: 7.5rem;
        height: 1.4rem;
        position: absolute;
        left: 0;
        bottom:0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 0.4rem;
        .icon{
            fill: #ffffff;
            width: 0.5rem;
            height: 0.5rem;
        }
        .play{
            width: 0.8rem;
            height: 0.8rem;
        }
    }
    .playLyric{
        position: absolute;
        width: 7.5rem;
        height: 8rem;
        top: calc(50% - 4rem);
        text-align: center;
        overflow: scroll;
        color: #ffffff;
        /*padding: 0.2rem 0;*/
        .active{
            color:red;
        }
    }
}
</style>